import React, { Component } from 'react';
import { connect } from 'react-redux'
import _ from 'lodash'
import image from './calculating.png';
import './App.css';
import {
  NUMBER_OF_GROUPS,
  clearNotice,
  buyAMiner,
  makeAGuess, PRICES_OF_MINER, inputText
} from "./reducers/blockchain";
import Notice from './Notice'

class App extends Component {
  shouldComponentUpdate(nextProps) {
    return true;
  }

  renderGroups() {
    let { group } = this.props;
    let balances = this.props.balances.toJS();
    let miners = this.props.miners.toJS();
    return <table>
      <tr className="header-row">
        <th>组</th>
        {
          _.map(_.range(NUMBER_OF_GROUPS), (n) => {
            return <th className={group === n ? 'active-group' : undefined}>{ n + 1 }</th>
          })
        }
      </tr>
      <tr>
        <td>钱钱</td>
        {
          _.map(_.range(NUMBER_OF_GROUPS), (n) => {
            const b = balances[n];
            return <td className={group === n ? 'active-group' : undefined}>{ b }</td>
          })
        }
      </tr>
      <tr>
        <td>矿机</td>
        {
          _.map(_.range(NUMBER_OF_GROUPS), (n) => {
            const list = miners[n];
            return <td className={group === n ? 'active-group' : undefined}>{ list.length }</td>
          })
        }
      </tr>
    </table>
  }

  renderBlocks() {
    let blocks = this.props.blocks.toJS();
    return _.map(_.reverse(blocks), (block) => {
      return <div className="block" key={block.round}>
        <p>block {block.round}</p>
        {
          (() => {
            if (block.winner == null) {
              return <img src={image} />
            } else {
              return <div className="block-body">
                <div>第{block.winner + 1}组</div>
              </div>
            }
          })()
        }
      </div>
    })
  }

  renderPlayerHeader() {
    let {
      group,
      attempts,
    } = this.props;
    let balances = this.props.balances.toJS();
    let miners = this.props.miners.toJS();
    return (
      <div className="player-balance">
        <div style={{fontWeight: 'bold'}}>
          第{group + 1}组 / 第{attempts}台矿机
        </div>
      </div>
    )
  }

  render() {
    const {
      notice,
      min,
      max,
      guess,
      buyMiner,
      clear,
      group,
      input,
      inputText,
    } = this.props;
    let miners = this.props.miners.toJS();
    let blocks = this.props.blocks.toJS();

    if (notice) {
      alert(notice);
      clear();
    }
    return (
      <div className="app">
        <Notice />

        <div className="balance-table">
          { this.renderGroups() }
        </div>

        <div className="player">
          { this.renderPlayerHeader() }
          <div className="operations">
            <input type="password" placeholder={`${min} ~ ${max}`} value={input} onChange={(e) => {
              inputText(e.target.value)
            }} />
            <a href="#" onClick={() => {guess(input)}}>挖挖挖</a>
            <a href="#" onClick={() => {buyMiner()}}>
              { `买矿机！ ${PRICES_OF_MINER[miners[group].length]}JB` }
            </a>
          </div>
        </div>

        <div className="blocks">
          { this.renderBlocks() }
        </div>
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    blocks: state.get('blockchain').get('blocks'),
    balances: state.get('blockchain').get('balances'),
    miners: state.get('blockchain').get('miners'),
    group: state.get('blockchain').get('group'),
    attempts: state.get('blockchain').get('attempts'),
    input: state.get('blockchain').get('input'),
    min: state.get('blockchain').get('min'),
    max: state.get('blockchain').get('max'),
  };
}

function mapDispatchToProps(dispatch) {
  return {
    clear: () => {
      dispatch(clearNotice())
    },
    guess: (number) => {
      dispatch(makeAGuess(number))
    },
    buyMiner: () => {
      dispatch(buyAMiner())
    },
    inputText: (text) => {
      dispatch(inputText(text))
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



