import React from 'react';
import ReactDOM from 'react-dom';
import Immutable from 'immutable'
import registerServiceWorker from './registerServiceWorker';
import { createStore, applyMiddleware } from 'redux'
import { Provider } from 'react-redux'
import { createLogger } from 'redux-logger'

import './index.css';

import App from './App';
import reducers from './reducers'
import blockchain from "./reducers/blockchain";

const logger = createLogger({
  stateTransformer: state => state.toJS()
});

const wrapper = (state, action) => {

  let localStored = localStorage.getItem('jbm');
  if (localStored) {
    state = Immutable.fromJS(JSON.parse(localStored));
  }

  const nextState = reducers(state, action);

  localStorage.setItem('jbm', JSON.stringify(nextState.toJS()));

  return nextState;
};

const store = createStore(
  wrapper,
  applyMiddleware(logger)
);

ReactDOM.render(
  <Provider store={store}><App /></Provider>,
  document.getElementById('root'));
