import React, { Component } from 'react';
import { connect } from 'react-redux'
import _ from 'lodash'
import image from './calculating.png';
import './App.css';
import {
  NUMBER_OF_GROUPS,
  PRICES_OF_MINER,
  buyAMiner,
  makeAGuess,
  inputText
} from "./reducers/blockchain";
import Notice from './Notice'

class App extends Component {
  shouldComponentUpdate(nextProps) {
    return true;
  }

  renderPlayerHeader() {
    let group = this.props.group;
    let balances = this.props.balances.toJS();
    let miners = this.props.miners.toJS();
    return (
      <div className="player-balance">
        <div style={{fontWeight: 'bold', fontSize: 18}}>第{group + 1}组矿工</div>
        <div>钱钱: {balances[group]} | 矿机: {miners[group].length}</div>
      </div>
    )
  }

  render() {
    const {
      group,
      input,
      inputText,
      guess,
      buyMiner,
    } = this.props;
    let miners = this.props.miners.toJS();
    return (
      <div className="app">
        { this.renderPlayerHeader() }
        <Notice />
        <div className="operations">
          <input type="text" placeholder="猜吧" value={input} onChange={(e) => {
            inputText(e.target.value)
          }} />
          <a href="#" onClick={() => {guess(input)}}>我挖</a>
          <a href="#" onClick={() => {buyMiner()}}>
            { `买矿机！ ${PRICES_OF_MINER[miners[group].length]}JB` }
          </a>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  const params = new URLSearchParams(ownProps.location.search);
  const group = params.get('group');
  return {
    group: parseInt(group),
    input: state.get('blockchain').get('input'),
    balances: state.get('blockchain').get('balances'),
    miners: state.get('blockchain').get('miners'),
  };
}

function mapDispatchToProps(dispatch) {
  return {
    inputText: (text) => {
      dispatch(inputText(text))
    },
    guess: (number) => {
      dispatch(makeAGuess(0, number))
    },
    buyMiner: () => {
      dispatch(buyAMiner(0))
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



