import _ from 'lodash'
import Immutable from 'immutable'

const INPUT_TEXT = "INPUT_TEXT";
const CLEAR_NOTICE = "CLEAR_NOTICE";
const MAKE_A_GUESS = "MAKE_A_GUESS";
const BUY_NEW_MINNER = "BUY_NEW_MINNER";

export const NUMBER_OF_GROUPS = 7;
const WINNER_PRICE = 50;
export const PRICES_OF_MINER = [0, 50, 70, 90, 110, 130, 150, 170, 190];

class Block {
  constructor(round, balances, offset = 0) {
    const sum = _.reduce(balances, (sum, item) => {
      return sum + item;
    });

    this.round = round;
    const delta = 10 + (round - 1) * 5;
    this.token = (_.random(0, delta) + sum) % delta + offset;
    this.winner = null;
  }
}

function initState() {
  const balances = _.times(NUMBER_OF_GROUPS, _.constant(0));
  const firstBlock = new Block(1, balances);
  const miners = _.map(_.range(NUMBER_OF_GROUPS), () => {
    return [createMiner()]
  });
  return Immutable.fromJS({ // TODO: idk why, but firstBlock won't be immutable
    input: '',
    blocks: [firstBlock],
    numberOfGroups: NUMBER_OF_GROUPS,
    balances: balances,
    miners: miners,
    notice: '',
    group: 0,
    attempts: 1,
    min: 0,
    max: 10,
  });
}

export default function blockchainReducer(state = initState(), action) {
  const round = currentRound(state);
  switch(action.type) {
  case INPUT_TEXT:
    return state.set('input', action.payload.text);
  case CLEAR_NOTICE:
    return state.set('notice', '');
  case MAKE_A_GUESS:
    const block = state.get('blocks').get(round - 1).toJS();
    const guessingGroup = state.get('group');
    if (parseInt(block.token + '') === parseInt(action.payload.number)) { // correct
      const max = 10 + round * 5 + parseInt(block.token);
      // set winner of block
      state = state.updateIn(['blocks', round - 1],  block => {
        return block.set('winner', guessingGroup);
      });
      // change balance of winner
      state = state.updateIn(['balances', guessingGroup], balance => {
        return balance + WINNER_PRICE;
      });
      // create new next block
      state = state.update('blocks', blocks => {
        return blocks.push(new Block(round + 1, state.get('balances').toJS(), block.token));
      });
      // set attempts back to zero
      state = state.set('attempts', 1);
      // update UI states
      return state.merge({
        'notice': `✅ 猜对了！\n通过钱钱等信息计算，下个随机数在${block.token}到${max}之间`,
        'input': '',
        'min': block.token,
        'max': max,
      });
    } else {
      const attempts = state.get('attempts');
      const numberOfOwnedMiner = state.getIn(['miners', guessingGroup]).size;
      if ((attempts + 1) > numberOfOwnedMiner) {
        state = state.merge({group: nextGroup(guessingGroup), attempts: 1})
      } else {
        state = state.merge({attempts: attempts + 1})
      }
      return state.merge({'notice': '❌ 猜错啦', 'input': ''});
    }
  case BUY_NEW_MINNER:
    const buyingGroup = state.get('group');
    const numberOfOwnedMiner = state.getIn(['miners', buyingGroup]).size;
    // check if afford
    if (state.getIn(['balances', buyingGroup]) < PRICES_OF_MINER[numberOfOwnedMiner]) {
      return state.set('notice', '你们钱不够的啊');
    }
    // decrease balance from buyer
    state = state.updateIn(['balances', buyingGroup], balance => balance - PRICES_OF_MINER[numberOfOwnedMiner]);
    // add number of miners to buyer
    state = state.updateIn(['miners', buyingGroup], miners => {
      return miners.push(createMiner());
    });
    // update UI states
    return state.merge({'notice': 'nb 买了个新矿机！'});
  default:
    return state
  }
}

function currentRound(state) {
  return state.get('blocks').size;
}

function createMiner() {
  return _.random(1000, 10000) + ''
}

function nextGroup(current) {
  var next = current + 1;
  if (next > NUMBER_OF_GROUPS -1) {
    next = 0;
  }
  return next
}

//
// ACTIONS
//

export function inputText(text) {
  return {
    type: INPUT_TEXT,
    payload: { text },
  }
}

export function clearNotice() {
  return {
    type: CLEAR_NOTICE,
  }
}

export function makeAGuess(number) {
  return {
    type: MAKE_A_GUESS,
    payload: { number },
  }
}

export function buyAMiner() {
  return {
    type: BUY_NEW_MINNER,
  }
}



